import React from "react";
import styled from "styled-components";
import Countdown from "react-countdown";
import { useWeb3React } from "@web3-react/core";
import { ReferralLink } from "../../components/ReferralLink";
import { supportedChain } from "../../utils";
import { Connect } from "../../components/Connect";
import { CountdownRenderer } from "../../components/CountdownRenderer";
import { generationStartDate } from "../../constants";
import Logo from "../../assets/images/logo.png";

const Wrapper = styled.div`
  padding: 9em 1em 1em 1em;
  display: grid;
  grid-gap: 1.5em;
  max-width: 50em;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    max-width: 100%;
    font-size: 0.75em;
    padding: 3em 1em 1em 1em;
 `};
`;

const Title = styled.div`
  font-size: 2.5em;
  text-align: center;
  ${({ theme }) => theme.mediaWidth.upToSmall`
  font-size: 2em;
`};
`;


const Text = styled.div`
  padding-top: 1em;
  font-size: 1.25em;
  text-align: center;
  color: ${({ theme }) => theme.primary1};
`;

const Description = styled.div`
  padding: 0.25em;
  line-height: 1.25em;
  text-align: justify;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    font-size: 1.15em;
 `};
`;

const LogoText = styled.div`
  font-size: 3.5em;
  white-space: nowrap;
  width: fit-content;
  font-weight: 600;

  ${({ theme }) => theme.mediaWidth.upToSmall`
  font-size: 3em;
`};
`;

const LogoWrapper = styled.div`
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1em;
  align-items: center;
  justify-content: center;
  user-select: none;
`;

const LogoImage = styled.img`
  width: 94px;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    width:70px;
  `};
`;

const ReferralLinkWrapper = styled.div`
  padding-top:1em;
  display: grid;
  grid-gap: 1.5em;
`;

export const ComingSoon = () => {
  const { account, chainId } = useWeb3React();
  return (
    <Wrapper>
      <LogoWrapper>
        <LogoImage src={Logo} alt="logo" />
        <LogoText>upBNB</LogoText>
      </LogoWrapper>
      
      <Title>It goes up against BNB</Title>
      <Text>Market Generation Event starts in</Text>
      <Countdown date={generationStartDate} renderer={CountdownRenderer} />
      <Description>
        upTokens are ERC-20 tokens that are mathematically engineered to go up
        against their equivalent counterparts using ERC-31337's token standard
        technology.
      </Description>
      <Description>
        upBNB is the second of many upTokens. ROOT holders will vote in the
        DAO to decide on the next upToken being launched and its parameters.
      </Description>
      <Description>
        Our Market Generation Event (M.G.E.) is the fairest possible way to
        raise funds with any token. It creates a permanently locked Uniswap
        trading pair using all collected value and distributes 80% of funds
        raised back to contributors. All this is done without affecting the
        market liquidity. Extra features include a built-in referral system and
        customizable vesting period.
      </Description>
      <ReferralLinkWrapper>
      {account && supportedChain(chainId) ? 
       <ReferralLink address={account!}/>
       : <Connect />}
        </ReferralLinkWrapper>
    </Wrapper>
  );
};
